<template>
    <div class="answer-hot">
        <div v-loading="isLoading" :style="{marginTop: isLoading ? '100px' : ''}">
            <div class="img-box" v-if="imgList.length > 0">
                <div v-for="(item, i) in imgList" :key="i">
                    <div class="item">
                        <img :src="item.profile_image" :onerror="errorImg" alt="图片">
                        <p class="name">{{ item.name }}</p>
                    </div>
                </div>
            </div>
            <div class="answer-item" v-for="(item, i) in dataList" :key="i">
                <h2>{{ item.author_name }}</h2>
                <span>{{ item.book_info}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dataList: [],
            imgList: [],
            isLoading: false,
            errorImg: 'this.src="' + require('./../../assets/img/default.png') + '"',
        }
    },
    methods: {
        async getData() {
            this.isLoading = true;
            let ret = await this.$http.getHotList();
            this.isLoading = false;
            if (ret.status === 0) {
                this.dataList = ret.data.list;
                this.imgList = ret.data.imgList;
            }
        }
    },
    created() {
        this.getData();
    }
}

</script>

<style lang="less" scoped>
    @import '../../assets/css/normalize.less';
    .answer-hot {
        .answer-item{
            float: left;
            width: 750px;
        }

        .img-box {
            margin-top: 25px;
            float: right;
            width: 318px;
            // border: 1px solid #e4e4e4;
            display: flex;
            flex-wrap: wrap;
            // justify-content: space-around;
            .item {
                width: 80px;
                height: 100px;
                margin-bottom: 10px;
                margin-right: 20px;
                img {
                    width: 70px;
                    height: 70px;
                    margin: 0 auto;
                    border-radius: 50%;
                }
                .name {
                    text-align: center;
                    line-height: 32px;
                }
            }
        }
    }
</style>



